import axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react'
import { GiTrophyCup } from 'react-icons/gi';
import Parallaxdata from "../../../data/home/parallax.json"
import environment from '../../../environment';
import './Parallax.css'

function Parallax() {
  const [Data, setData] = useState({});
   
    
  let getUrl = `${environment.baseUrl}/api/about/all`;
   useEffect(()=>{
       axios.get(getUrl).then(resp=>{
           console.log("got data", resp)
           if(resp.data.success){
           setData(resp.data.data[0]);
           }
       }).catch(e=>{ console.log("Error ", e)})
     },[]
   )


  return (
    <>
     <div className='row justify-content-center align-items-center mt-5'>
        <div className='col-10 parllax '>
            <div className='row justify-content-center align-items-center m-5 p-4'>
            {Data? (
              <Fragment>
                    <div className='col-lg-3 d-flex justify-content-center'>
                    <div className='parallax-icon'>
                        <GiTrophyCup/> 
                    </div>
                    <div className='ml-2'>
                    <p className='parallax-number'>{Data?.teachers}</p>
                        <p className='parallax-text'>Teachers</p>
                   
                    </div>
                  
                </div>
                 <div className='col-lg-3 d-flex justify-content-center'>
                 <div className='parallax-icon'>
                     <GiTrophyCup/> 
                 </div>
                 <div className='ml-2'>
                 <p className='parallax-number'>{Data?.students}</p>
                     <p className='parallax-text'>Students</p>
                
                 </div>
               
             </div>
              <div className='col-lg-3 d-flex justify-content-center'>
              <div className='parallax-icon'>
                  <GiTrophyCup/> 
              </div>
              <div className='ml-2'>
              <p className='parallax-number'>{Data?.courses}</p>
                  <p className='parallax-text'>Courses</p>
             
              </div>
            
          </div>
           <div className='col-lg-3 d-flex justify-content-center'>
           <div className='parallax-icon'>
               <GiTrophyCup/> 
           </div>
           <div className='ml-2'>
           <p className='parallax-number'>{Data?.years}</p>
               <p className='parallax-text'>Years Exp.</p>
          
           </div>
         
       </div>
       </Fragment>
                ):null}


            </div>
        </div>
    </div>
    </>
  )}
  export default Parallax