import axios from 'axios';
import React, { useEffect, useState } from 'react'
// import Data from '../../../data/home/upcomingevent.json'
// import Upcomingdataslider from '../../../data/home/upcomingeventslider.json'
import Swiper from "react-id-swiper";
import Event from '../../../assets/images/banner/upcoming-event.jpg'
import environment from '../../../environment';
import './Upcomingevent.css'


function Upcomingevent() {
  const params = {
    loop: true,
    speed: 1000,
    autoplay: {
      delay: 5000000,
      disableOnInteraction: false
    },
    watchSlidesVisibility: true,
    pagination: {
      el: ".swiper-pagination",
      clickable: true
    },
    
  };


  const baseUrl = environment.baseUrl;
  let getUrl = `${baseUrl}/api/dates/all`;
 
    const [Upcomingdataslider, setSlider] = useState([]);
    const [Data, setData] = useState([])
    const [dataLoaded, setDataLoaded] = useState(false)


  function  getMonthYear(dtStr){
     const mnthStr = ["JAN","FEB","MAR","APR", "MAY", "JUN", "JUL", "AUG","SEP","OCT","NOV","DEC"]
      const monthNum = new Date(dtStr).getMonth()
      const year = new Date(dtStr).getFullYear().toString().slice(2,4);
      return mnthStr[monthNum-1]+"-"+year;
    }

    function getDay(dtStr){
      const day = new Date(dtStr).getDay();
      return day;
    }


    useEffect(()=>{
        axios.get(getUrl).then(resp=>{
            console.log("got data", resp)
            if(resp.data.success){
            setSlider(resp.data.data);
            setData(resp.data.data[0]);
            setDataLoaded(true)
            }
        }).catch(e=>{ console.log("Error ", e)})
      },[]
    )

  return (
    <>
    <div className='row justify-content-center align-items-center mt-5'>
        <div className='col-10 upcomingevent py-5 px-3'>
    <div className="row upcoming-even-wrapper">
      <div className="col-lg-6">
        <h2>{Data?.title}</h2>
        <div className="event-img">
          <span>{Data?.date}</span>
          <img src={Event} className="event-image-img" alt="event-img" />
          <div>
          <div className=' event-img-text p-3'>
            <h3 className='pb-0 mb-0 text-white'>{Data?.title}</h3>
            <div className='border-title-img'>
          </div>
            <p className='mt-3 text-white'>{Data?.content}</p>
          </div>
          </div>
    
        </div>
       
      </div>
      <div className="col-lg-6 mt-5 mt-lg-0">
        <h2>{Data?.title}</h2>

{dataLoaded?(<Swiper {...params}>
    {Upcomingdataslider &&
      Upcomingdataslider.map((single, key) => {
        
        return (
<div key={key}>


          <div className='row event-list d-flex'>
          <div className='col-3 col-lg-2 event-date'>
          <div className='event-date-color'>
            <div className='d-flex flex-column align-items-center justify-content-center event-border-dash  m-1 '>
              <span className='event-date1'>{getDay(single.date)}</span>
              <span style={{textTransform:"capitalize"}} className='event-date2'>{getMonthYear(single.date)}</span>
            </div>
          </div>
          </div>
          <div className='col-9 col-lg-10 event-text'>
            <h3 className='pb-0 mb-0'>{single.title}</h3>
            <div className='border-title'>

    </div>
            <p className='mt-md-3'>{single.content}</p>
          </div>
      </div>

      {/* <div className='d-flex justify-content-center m-3'>
        <div className='border-class'>

        </div>
       </div>

      <div className='row event-list d-flex'>
          <div className='col-3 col-lg-2 event-date'>
          <div className='event-date-color'>
            <div className='d-flex flex-column align-items-center justify-content-center event-border-dash  m-1 '>
              <span className='event-date1'>{single.date}</span>
              <span className='event-date2'>{single.month}</span>
            </div>
          </div>
          </div>
          <div className='col-9 col-lg-10 event-text'>
            <h3 className='pb-0 mb-0'>{single.eventtitle}</h3>
            <div className='border-title'>

    </div>
            <p className='mt-md-3'>{single.text}</p>
          </div>
      </div> */}
      </div>
        );
      })}
      
    </Swiper> ):null}









 
      </div>
    </div>
  

        </div>
    </div>


    </>
  )}
  export default Upcomingevent