import React from 'react';
import { Link } from 'react-router-dom';

const Logo = () => {
  return (
    <div className='logo'>
      <Link to={ '/'}>
        <h1 className='text-white'>LOGO</h1>
        {/* <img style={{ width: 150, height: 150 }} alt='Logo' src='https://emeraldheights.edu.in/wp-content/uploads/2017/04/JPG-Emerald-Logo.jpg' /> */}
      </Link>
    </div>
  );
};

export default Logo;
