import React, { Fragment, useEffect, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";

import { admSchema } from "../../../yupSchema/admSchema";
import environment from "../../../environment";
import { dateSchema } from "../../../yupSchema/dateSchema";
import { infraSchema } from "../../../yupSchema/infraSchema";
import { newsSchema } from "../../../yupSchema/newsSchema";
import AdmCard from "../../containers/administration/card/AdmCard";
import InfraCard from "../../containers/infra/card/InfraCard";
import NewsCard from "../../containers/news/card/NewsCard";
import DatesCard from "../../containers/dates/card/DatesCard";
import "./AdminForm.css";
import { aboutSchema } from "../../../yupSchema/aboutSchema";
import { contactSchema } from "../../../yupSchema/contactSchema";
import { welcomeSchema } from "../../../yupSchema/welcomeSchema";

const AdminForm = ({ pageType, formType }) => {
  const [successMessage, setSuccessMessage] = useState({ message: "" });
 const [isSpinner, setSpinner] = useState(false)
  const [isCreate, setCreate] = useState(false);
  const [isEdit, setEdit] = useState(false);
  const [dataWithId, setDataWithId] = useState("");
  const [isDate, setDate] = useState(false);
  const [isAdm, setAdm] = useState(false);
  const [isInfra, setInfra] = useState(false);
  const [isNews, setNews] = useState(false);
  const [isContact, setContact] = useState(false);
  const [isWelcome, setWelcome] = useState(false);
  const [isAbout, setAbout] = useState(false);
  const [isSingle, setSingle] = useState(false)

  // fields
  const [isImg, setImg] = useState(true);
  const [isTitle, setTitle] = useState(true);
  const [isContent, setContent] = useState(true);
  const baseUrl = environment.baseUrl;
  const [file, setFile] = useState(null);
  function addImage(e) {
    const file = e.target.files[0];
    console.log(file);
    setFile(file);
  }
  let title;
  let initialValues;
  let url = `${baseUrl}/api/${pageType}/create-new`;
  if (isEdit) url = `${baseUrl}/api/${pageType}/update/${dataWithId._id}`;
  let schema;
  if (pageType === "adm") {
    title = "Administration";
    initialValues = {
      author: dataWithId?.author || "",
      degree: dataWithId?.degree || "",
      experience: dataWithId?.experience || "",
    };
    schema = admSchema;
  }

  if (pageType === "infra") {
    title = "Infrastucture and Facility";
    initialValues = {
      title: dataWithId?.title || "",
      content: dataWithId?.content || "",
    };
    schema = infraSchema;
  }
  if (pageType === "dates") {
    title = "New Dates";
    initialValues = {
      date: dataWithId?.date,
      title: dataWithId?.title || "",
      content: dataWithId?.content || "",
    };
    schema = dateSchema;
  }
  if (pageType === "news") {
    title = "All news";
    initialValues = {
      title: dataWithId?.title || "",
      content: dataWithId?.content,
    };
    schema = newsSchema;
  }

  if (pageType === "about") {
    title = "About";
    initialValues = {
      teachers: dataWithId?.teachers || "",
      students: dataWithId?.students || "",
      courses: dataWithId?.courses || "",
      years: dataWithId?.years || "",
    };
    schema = aboutSchema;
  }
  if (pageType === "contact") {
    title = "Contact";
    initialValues = {
      address: dataWithId?.address || "",
      email: dataWithId?.email || "",
      phone: dataWithId?.phone || "",
      fb: dataWithId?.fb || "",
      in: dataWithId?.in || "",
      twt: dataWithId?.twt || "",
    };
    schema = contactSchema;
  }
  if (pageType === "welcome") {
    title = "Welcome";
    initialValues = { content: dataWithId?.content || "" };
    schema = welcomeSchema;
  }
  if (pageType === "logo") {
    title = "Logo";
    // initialValues ={ title:dataWithId?.title || "", content:dataWithId?.content };
    // schema = newsSchema;
  }
  if (pageType === "gallery") {
    title = "Gallery";
    // initialValues ={ title:dataWithId?.title || "", content:dataWithId?.content };
    // schema = newsSchema;
  }

  let getUrl = `${baseUrl}/api/${pageType}/all`;

  const [datas, setData] = useState([]);

  useEffect(() => {
    if (pageType === "dates") {
      setDate(true);
      setImg(false);
    } else if (pageType === "adm") {
      setContent(false);
      setTitle(false);
      setAdm(true);
    } else if (pageType === "infra") {
      setInfra(true);
    } else if (pageType === "news") {
      setNews(true);
    } else if (pageType === "about") {
      setImg(false);
      setContent(false);
      setTitle(false);
      setAbout(true);
      setSingle(true)
    } else if (pageType === "contact") {
      setImg(false);
      setContent(false);
      setTitle(false);
      setContact(true);
      setSingle(true)
    } else if (pageType === "welcome") {
      setImg(false);
      setTitle(false);
      setWelcome(true);
      setSingle(true)
    }

    axios
      .get(getUrl)
      .then((resp) => {
        console.log("got data", resp);

        setData(resp.data.data);
        if ((pageType === "contact" || pageType=== "about" || pageType=== "welcome") && resp.data.data.length>0) {
          setEdit(true);
          setCreate(true);
          setDataWithId(resp.data.data[0]);
        }
        console.log(datas);
      })
      .catch((e) => {
        console.log("Error ", e);
      });
  }, [successMessage]);

  const delHandle = (id) => {
    if (window.confirm("Are you sure you want to delete?") === true) {
      console.log(id, "delete Id");
      const deleteUrl = `${baseUrl}/api/${pageType}/delete/${id}`;
      axios
        .get(deleteUrl)
        .then((resp) => {
          console.log("Deleted");
          if (resp.data.success) {
            setSuccessMessage({ message: resp.data.message });
          }
        })
        .catch((e) => {
          console.log("Error", e);
        });
    }
  };

  const editForm = (id) => {
    setCreate(true);
    setEdit(true);
    const data = datas.filter((x) => x._id === id);
    setDataWithId(data[0]);
  };

  const Formik = useFormik({
    enableReinitialize: true,
    initialValues: initialValues,
    validationSchema: schema,
    onSubmit: (values) => {
      setSpinner(true)
      const fd = new FormData();
      if (isImg && !isEdit) {
        fd.append("image", file, file.name);
      }
      if (pageType === "dates") {
        fd.append("date", values.date);
      }
      if (pageType === "adm") {
        fd.append("author", values.author);
        fd.append("degree", values.degree);
        fd.append("experience", values.experience);
      } else if (pageType === "contact") {
        fd.append("address", values.address);
        fd.append("email", values.email);
        fd.append("phone", values.phone);
        fd.append("in", values.in);
        fd.append("fb", values.fb);
        fd.append("twt", values.twt);
      } else if (pageType === "about") {
        fd.append("teachers", values.teachers);
        fd.append("students", values.students);
        fd.append("courses", values.courses);
        fd.append("years", values.years);
      } else {
        fd.append("title", values.title);
        fd.append("content", values.content);
      }

      if (isEdit) {
        console.log(fd);
        axios
          .patch(url, fd)
          .then((resp) => {
            if (resp.data.success) {
              setSuccessMessage({ message: resp.data.message });
              console.log(successMessage, "Successs MEssaage");
              setCreate(false);
              
            }
            Formik.resetForm();
            setSpinner(false)
          })
          .catch((err) => {
            console.log(err, "error");
            setSpinner(false)
          });
      } else {
        axios
          .post(url, fd)
          .then((resp) => {
            if (resp.data.success) {
              setSuccessMessage({ message: resp.data.message });
              console.log(successMessage, "Successs MEssaage");
              setCreate(false);
            }
            Formik.resetForm();
          })
          .catch((err) => {
            console.log(err, "error");
          });
      }
    },
  });

  return (
    <Fragment>
      <h2 className="text-center bg-white">
        <span className="text-white bg-info">Admin</span>
        {title}
      </h2>
      {isCreate ? (
        <div className="w-100 bg-primary position-relative">
        {isCreate && !isSingle?<button
            style={{ zIndex: "100", right: "0" }}
            className="btn btn-sm btn-danger position-absolute d-flex"
            onClick={() => {
              setCreate(false);
            }}
          >
            X
          </button>:null}  

          <div
            className="container-form-registration 
      m-auto wi-100"
          >
            <form
              onSubmit={Formik.handleSubmit}
              style={{ zIndex: "99" }}
              className="w-100 p-5 d-flex flex-column bg-white position-absolute w-75 "
              method="POST"
            >
              {isAdm ? (
                <div className="mb-3">
                  <label className="form-label">Author</label>
                  <input
                    type="text"
                    name="author"
                    className="form-control"
                    value={Formik.values.author}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Author *"
                  />
                  {Formik.touched.author && Formik.errors.author ? (
                    <p className="form-error">{Formik.errors.author}</p>
                  ) : null}
                </div>
              ) : null}
              {isTitle ? (
                <div className="mb-3">
                  <label className="form-label">Title</label>
                  <input
                    type="text"
                    name="title"
                    className="form-control"
                    value={Formik.values.title}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Title *"
                  />
                  {Formik.touched.title && Formik.errors.title ? (
                    <p className="form-error">{Formik.errors.title}</p>
                  ) : null}
                </div>
              ) : null}

              {isDate ? (
                <div className="mb-3">
                  <label className="form-label">Date</label>
                  <input
                    type="date"
                    name="date"
                    className="form-control"
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Image"
                  />
                </div>
              ) : null}

              {isImg ? (
                <div className="mb-3">
                  {isEdit ? (
                    <img
                      src={`../upload/${dataWithId?.imgUrl}`}
                      height="80px"
                      alt={"Image"}
                    />
                  ) : (
                    <>
                      <label className="form-label">Image</label>
                      <input
                        type="file"
                        name="image"
                        className="form-control"
                        onChange={(event) => {
                          addImage(event);
                        }}
                        placeholder="Image"
                      />
                    </>
                  )}
                </div>
              ) : null}

              {isAdm ? (
                <div className="mb-3">
                  <label className="form-label">Degree</label>
                  <input
                    type="text"
                    name="degree"
                    className="form-control"
                    value={Formik.values.degree}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Degree *"
                  />
                  {Formik.touched.degree && Formik.errors.degree ? (
                    <p className="form-error">{Formik.errors.degree}</p>
                  ) : null}
                </div>
              ) : null}
              {isContent ? (
                <div className="mb-3">
                  <label className="form-label">Content</label>
                  <textarea
                    type="text"
                    name="content"
                    className="form-control"
                    value={Formik.values.content}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Content *"
                  ></textarea>
                  {Formik.touched.content && Formik.errors.content ? (
                    <p className="form-error">{Formik.errors.content}</p>
                  ) : null}
                </div>
              ) : null}

              {isAdm ? (
                <div className="mb-3">
                  <label className="form-label">Experience</label>
                  <input
                    type="number"
                    name="experience"
                    className="form-control"
                    value={Formik.values.experience}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Experience *"
                  />
                  {Formik.touched.experience && Formik.errors.experience ? (
                    <p className="form-error">{Formik.errors.experience}</p>
                  ) : null}
                </div>
              ) : null}

{isContact ? (
                <Fragment>
                <div className="mb-3">
                  <label className="form-label">Address</label>
                  <input
                    type="text"
                    name="address"
                    className="form-control"
                    value={Formik.values.address}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Address *"
                  />
                  {Formik.touched.address && Formik.errors.address ? (
                    <p className="form-error">{Formik.errors.address}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Email</label>
                  <input
                    type="email"
                    name="email"
                    className="form-control"
                    value={Formik.values.email}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Email *"
                  />
                  {Formik.touched.email && Formik.errors.email ? (
                    <p className="form-error">{Formik.errors.email}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Phone</label>
                  <input
                    type="text"
                    name="phone"
                    className="form-control"
                    value={Formik.values.phone}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Phone *"
                  />
                  {Formik.touched.phone && Formik.errors.phone ? (
                    <p className="form-error">{Formik.errors.phone}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Instagram</label>
                  <input
                    type="text"
                    name="in"
                    className="form-control"
                    value={Formik.values.in}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Instagram *"
                  />
                  {Formik.touched.in && Formik.errors.in ? (
                    <p className="form-error">{Formik.errors.in}</p>
                  ) : null}
                </div>

                <div className="mb-3">
                  <label className="form-label">Facebook</label>
                  <input
                    type="text"
                    name="fb"
                    className="form-control"
                    value={Formik.values.fb}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Facebook *"
                  />
                  {Formik.touched.fb && Formik.errors.fb ? (
                    <p className="form-error">{Formik.errors.fb}</p>
                  ) : null}
                </div>
                
                <div className="mb-3">
                  <label className="form-label">Twitter</label>
                  <input
                    type="text"
                    name="twt"
                    className="form-control"
                    value={Formik.values.twt}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Twitter *"
                  />
                  {Formik.touched.twt && Formik.errors.twt ? (
                    <p className="form-error">{Formik.errors.twt}</p>
                  ) : null}
                </div>
                </Fragment>
              ) : null}

{isAbout ? (
  <Fragment>
                <div className="mb-3">
                  <label className="form-label">Teachers</label>
                  <input
                    type="number"
                    name="teachers"
                    className="form-control"
                    value={Formik.values.teachers}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Teachers *"
                  />
                  {Formik.touched.teachers && Formik.errors.teachers ? (
                    <p className="form-error">{Formik.errors.teachers}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Students</label>
                  <input
                    type="number"
                    name="students"
                    className="form-control"
                    value={Formik.values.students}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Students *"
                  />
                  {Formik.touched.students && Formik.errors.students ? (
                    <p className="form-error">{Formik.errors.students}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Courses</label>
                  <input
                    type="number"
                    name="courses"
                    className="form-control"
                    value={Formik.values.courses}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Courses *"
                  />
                  {Formik.touched.courses && Formik.errors.courses ? (
                    <p className="form-error">{Formik.errors.courses}</p>
                  ) : null}
                </div>
                <div className="mb-3">
                  <label className="form-label">Years</label>
                  <input
                    type="number"
                    name="years"
                    className="form-control"
                    value={Formik.values.years}
                    onChange={Formik.handleChange}
                    onBlur={Formik.handleBlur}
                    placeholder="Years *"
                  />
                  {Formik.touched.years && Formik.errors.years ? (
                    <p className="form-error">{Formik.errors.years}</p>
                  ) : null}
                </div>
                </Fragment>
              ) : null}

              <button type="submit" className="btn btn-dark d-flex flex-row justify-content-center">
                Submit 
                {isSpinner?<div class="spinner-border text-primary ml-2" role="status">
  <span class="sr-only">Loading...</span>
</div>:null}
               
              </button>
              
            </form>
          </div>
        </div>
      ) : null}
      <button
        onClick={() => {
          setCreate(true);
        }}
      >
        Add New
      </button>

      {isAdm &&
        datas &&
        datas.map((data) => {
          return (
            <AdmCard
              data={data}
              key={data._id}
              del={delHandle}
              edit={editForm}
            />
          );
        })}

      {isInfra &&
        datas &&
        datas.map((data) => {
          return (
            //  <Card data={data} del={ delHandle}/>
            <InfraCard
              data={data}
              key={data._id}
              del={delHandle}
              edit={editForm}
            />
          );
        })}

      {isNews &&
        datas &&
        datas.map((data) => {
          return (
            //  <Card data={data} del={ delHandle}/>
            <NewsCard
              data={data}
              key={data._id}
              del={delHandle}
              edit={editForm}
            />
          );
        })}

      {isDate &&
        datas &&
        datas.map((data) => {
          return (
            //  <Card data={data} del={ delHandle}/>
            <DatesCard
              data={data}
              key={data._id}
              del={delHandle}
              edit={editForm}
            />
          );
        })}
    </Fragment>
  );
};

export default AdminForm;
