import React, { Fragment, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";

import { signUpSchema } from "../../../yupSchema/signupSchema";
import "./RegistrationForm.css";
import environment from "../../../environment";
import { loginSchema } from "../../../yupSchema/loginSchema";
import { Redirect, useHistory } from "react-router-dom";

const RegistrationForm = () => {
  const [isSignup, setSignup] = useState(false);
  const baseUrl = environment.baseUrl;
  const [errMessage, setErrorMessage] = useState("");

  function btnClicked(e, type) {
    if (type == "login") setSignup(false);
    if (type == "signup") setSignup(true);
    document.querySelectorAll(".btn-form").forEach((btn) => {
      btn.classList.remove("active");
    });
    e.target.className = "btn-form active";
  }

  let initialValues ={ email:"", password: ""};
  let schema = loginSchema;
  let url = `${baseUrl}/api/user/login-user`
  if(isSignup){
   initialValues = {
    email: "",
    username: "",
    password: "",
    cPassword: "",
  };

   schema = signUpSchema;
   url = `${baseUrl}/api/user/create-user`
}
  
const history = useHistory();
  const Formik = useFormik({
    initialValues: initialValues,
    validationSchema: schema,
    onSubmit: (values) => {
      axios
        .post(url, { values })
        .then((resp) => {
            localStorage.setItem("token", resp.headers.get("authorization"))
          Formik.resetForm();
          history.push("/admin")
        })
        .catch((err) => {
          console.log(err, "error");
          if(err.response.status==404){
            setErrorMessage("Server Error, Try after sometime!")
          }else{
            setErrorMessage(err.response.data.message)
          }
        });
    },
  });

  
  return (
    <Fragment>
      <div className="container-form-registration m-auto col-lg-8 col-md-10 col-sm-12 col-12">
        <div className="container-btn-form d-flex flex-row justify-content-center align-items-center">
          <button
            className="btn-form active"
            onClick={(event) => btnClicked(event, "login")}
          >
            Log In
          </button>
          <button
            className="btn-form"
            onClick={(event) => btnClicked(event, "signup")}
          >
            Sign Up
          </button>
        </div>

       
          <form onSubmit={Formik.handleSubmit} className="p-5 " method="POST">
            {isSignup?<h2>Sign Up</h2>:<h2>Login</h2>}

            {errMessage?<div className="d-flex flex-row justify-content-center ">
             <span className="text-danger" >{errMessage}</span> <button onClick={()=>{ setErrorMessage("")}}>X</button>
              </div>:null}
            <div className="mb-3">
              <label  className="form-label">
                Email address
              </label>
              <input
                type="email"
                name="email"
                className="form-control"
                id=""
                aria-describedby="emailHelp"
                value={Formik.values.email}
                onChange={Formik.handleChange}
                onBlur={Formik.handleBlur}
                placeholder="Email *"
              />
                   { Formik.touched.email && Formik.errors.email ?( <p className='form-error'>{Formik.errors.email}</p>):null }

            </div>
            {isSignup?(
            <div className="mb-3">
              <label  className="form-label">
                Username
              </label>
              <input
                type="text"
                name="username"
                className="form-control"
                id=""
                aria-describedby="emailHelp"
                value={Formik.values.username}
                onChange={Formik.handleChange}
                onBlur={Formik.handleBlur}
                placeholder="Username *"
              />
             { Formik.touched.username && Formik.errors.username ?( <p className='form-error'>{Formik.errors.username}</p>):null }
                  </div>
                  ):null}
            <div className="mb-3">
              <label  className="form-label">
                Password
              </label>
              <input
                type="password"
                name="password"
                className="form-control"
                id="exampleInputPassword1"
                value={Formik.values.password}
                onChange={Formik.handleChange}
                onBlur={Formik.handleBlur}
                placeholder="Password"
              />
                   { Formik.touched.password && Formik.errors.password ?( <p className='form-error'>{Formik.errors.password}</p>):null }
            </div>
            {isSignup?(
            <div className="mb-3">
              <label  className="form-label">
                Confirm Password
              </label>
              <input
                type="password"
                name="cPassword"
                className="form-control"
                id="exampleInputPassword1"
                value={Formik.values.cPassword}
                onChange={Formik.handleChange}
                onBlur={Formik.handleBlur}
                placeholder="Confirm Password"
              />
                   { Formik.touched.cPassword && Formik.errors.cPassword ?( <p className='form-error'>{Formik.errors.cPassword}</p>):null }
            </div>
            ):null}
            <button type="submit" className="btn btn-dark">
                  Submit
            </button>
          </form>
    
      </div>
    </Fragment>
  );
};

export default RegistrationForm;
