
import React from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import ScrollToTop from '../helpers/ScrollToTop';
import Dashbaord from './pages/Dashbaord';
import Dates from './pages/Dates';
import FacilityInfra from './pages/FacilityInfra';
import News from './pages/News';
import App from '../App';
import PrivateRoute from '../Guards/PrivateRoute';
import About from './pages/About';
import Contact from './pages/Contact';
import Logo from './pages/Logo';
import Gallery from './pages/Gallery';
import Welcome from './pages/Welcome';

function Admin({match}) {
  return (
    <Router>
      <ScrollToTop>
        <Switch>
        <PrivateRoute path={`${match.path}/administration`}><Dashbaord/></PrivateRoute>
          <PrivateRoute path={`${match.path}/dates`}><Dates/></PrivateRoute> 
          <PrivateRoute path={`${match.path}/facility-infra`} ><FacilityInfra/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/news`} ><News/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/about`} ><About/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/contact`} ><Contact/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/gallery`} ><Gallery/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/logo`} ><Logo/> </PrivateRoute>
          <PrivateRoute path={`${match.path}/welcome`} ><Welcome/> </PrivateRoute>
          <PrivateRoute path={`${match.path}`} ><Dashbaord/> </PrivateRoute>
          <Route path={`/`} component={App} />
        </Switch>
       
        {/*
        <Switch>
        <Route path={`admin/administration`} component={Dashbaord} />
          <Route path={`admin/dates`} component={Dates} />
          <Route path={`admin/facility-infra`} component={FacilityInfra} />
          <Route path={`admin/news`} component={News} />
          <Route path={`admin`} component={Dashbaord} />
          <Route path={`/`} component={App} />
        </Switch> */}

      </ScrollToTop>
    </Router>
  );
}

export default Admin;


