import React, { Fragment } from 'react'

const AdmCard = ({data, del, edit}) => {
    console.log("data is coming")
  return (
    <Fragment>

  <div key={data._id} className="p-3 card mt-3" style={{width: "100%"}}>
    <h3 className="text-center bg-primary text-white">{data.author}</h3>
    <div className="d-flex flex-row justify-content-between">
      <p className="w-25">{data.degree}({data.experience})</p>
      <div className='w-25 d-flex flex-column justify-content-center align-items-center'>
      <button className="btn btn-edit btn-sm btn-info"onClick={()=>{edit(data._id)}}>Edit</button>
      <button className="btn btn-delete btn-sm btn-danger" onClick={()=>{del(data._id)}}>Delete</button>
      </div>
     
    </div>
  </div>
  </Fragment>
  )
}

export default AdmCard