import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { animateScroll } from 'react-scroll';
import { GrFacebookOption } from 'react-icons/gr';
import { GrLinkedinOption } from 'react-icons/gr';
import { AiOutlineTwitter } from 'react-icons/ai';
import { MdExpandLess } from 'react-icons/md';
import Footerdata from '../../../data/home/footer.json';
import styles from './Footer.module.scss';
import axios from 'axios';
import environment from '../../../environment';

const Footer = ({ footerBg }) => {
  const [scroll, setScroll] = useState(0);
  const [top, setTop] = useState(0);

  useEffect(() => {
    setTop(100);
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    animateScroll.scrollToTop();
  };

  const handleScroll = () => {
    setScroll(window.scrollY);
  };


  const [Data, setData] = useState({});
   
    
  let getUrl = `${environment.baseUrl}/api/contact/all`;
   useEffect(()=>{
       axios.get(getUrl).then(resp=>{
           if(resp.data.success){
           setData(resp.data.data[0]);
           }
       }).catch(e=>{ console.log("Error ", e)})
     },[]
   )



  return (
    <footer className={`footer-area mt-5 ${footerBg === 'white' ? styles.footer2 : ''}`}>
      <div className={`${styles.footerTop} ${footerBg === 'white' ? 'bg--cart-7' : 'bg--cart-2'}`}>
        <div className='container footer-bg'>
          <div className='row'>
            {/* Start Single Wedget */}
            <div className='col-12 col-sm-6 col-lg-3'>
              <div className={styles.singleWidget}>
                <div className={styles.logo}>
                     <Link to={'/'}>
                    <h1 className='text-white text-center'>{Footerdata.logo}</h1>
                  </Link>
                </div>
                <div className={styles.content}>
                  <p className='text-center'>
                    {Footerdata.cpy1} &copy; {Footerdata.cpyright} <br />
                    {Footerdata.cpy2}
                  </p>
                </div>
              </div>
            </div>
            {/* End Single Wedget */}
            {/* Start Single Wedget */}
            <div className='col-12 col-sm-6 col-lg-3 offset-lg-1 xs__mt--40 d-flex align-items-center flex-column'>
              <div className={` ${styles.singleWidget}  `}>
                <h2 className={styles.ftTitle}>{Footerdata.nav}</h2>
                <ul className={styles.ftContactLink}>
                  <li>
                    <Link to='#'>{Footerdata.about}</Link>
                  </li>
                  <li>
                    <Link to='#'>{Footerdata.admission}</Link>
                  </li>
                  <li>
                    <Link to='#'>{Footerdata.academics}</Link>
                  </li>
                  <li>
                    <Link to='#'>{Footerdata.research}</Link>
                  </li>
                  <li>
                    <Link to='#'>{Footerdata.contact}</Link>
                  </li>
                </ul>
              </div>
            </div>
            {/* End Single Wedget */}
            {/* Start Single Wedget */}

            {/* End Single Wedget */}
            {/* Start Single Wedget */}
            <div className='col-lg-3 offset-lg-1 col-md-6 col-sm-6 col-12 sm__mt--40 md__mt--40'>
              <div className={styles.singleWidget}>
                <div className='footer-contact-text'>
                  <h2 className={styles.ftTitle}>{Footerdata.contact}</h2>

                  <p className='footer-text'>{Data?.address}</p>
                  <span className='footer-text'>{Data?.email}</span>
                  <br />
                  <span className='footer-text'>{Data?.phone}</span>

                  <div className={`${styles.ftSocialLink} mt-4`}>
                    <ul className={styles.socialIcon}>
                      <li>
                        <a href={`https://www.facebook.com/${Data?.fb}`}>
                          <GrFacebookOption />
                        </a>
                      </li>
                      <li>
                        <a href={`https://www.linkedin.com/${Data?.in}`}>
                          <GrLinkedinOption />
                        </a>
                      </li>
                      <li>
                        <a href={`https://www.twiter.com/${Data?.twt}`}>
                          <AiOutlineTwitter />
                        </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              {/* <div  className="map-placeholder" >
              <img src={Footerimg} alt=""/>

              </div> */}
            </div>
            {/* End Single Wedget */}
          </div>
        </div>
      </div>

      <button className={`scroll-top ${scroll > top ? 'show' : ''}`} onClick={() => scrollToTop()}>
        <MdExpandLess />
      </button>
    </footer>
  );
};

Footer.propTypes = {
  footerBg: PropTypes.string
};

export default Footer;
